import React from 'react'
import DistanceRenderer from './DistanceRenderer'
import { FriendTitle } from '../styles'

//var distance = require('google-distance-matrix');
const DistanceContainer = ({hasRealFriends, hasOneDest, hasMultipleDest, friends, destination}) => {
    return (
    <div>
        <FriendTitle style={{fontSize:"20px"}}>Average Driving Time</FriendTitle>
        <p>{hasRealFriends && hasOneDest && 
            <DistanceRenderer friends={friends} destination={destination}></DistanceRenderer>}</p>
        <button>Save</button>

        <ul><li>past locations wooo</li></ul>
    </div>
    )
}

export default DistanceContainer
