/* global google */
import React from "react";

class DistanceRenderer extends React.Component {
    state = {
        distanceList: [],
        error: null
    };
    componentDidMount(){
        const {friends, destination} = this.props;
        var origins = friends.map( friend => {
            if(friend.location.length){
                return JSON.parse(friend.location).geometry.location
            }
            return undefined
        });
        
        var destinations = [JSON.parse(destination).geometry.location]
        
        var distance = new google.maps.DistanceMatrixService();
        distance.getDistanceMatrix(
            {
                origins:origins,
                destinations:destinations,
                travelMode:'DRIVING',
                avoidTolls:true
            
            }, (result, status) => {
                console.log("distance response", result, status)
                if (status !== google.maps.DirectionsStatus.OK) {
                    this.setState({error: result});
                }
                if(!result) {
                    return console.log('no distances');
                }
                if (status === google.maps.DirectionsStatus.OK) {
                    const distances = result
                    for (var i=0; i < origins.length; i++) {
                        for (var j = 0; j < destinations.length; j++) {
                            var origin = distances.originAddresses[i];
                            var destination = distances.destinationAddresses[j];
                            if (distances.rows[0].elements[j].status === 'OK') {
                                var distance = distances.rows[i].elements[j].distance.text;
                                this.setState({distanceList: [...this.state.distanceList, distance]});
                            } else {
                                console.log(destination + ' is not reachable by land from ' + origin);
                            }
                        }
                    }
                }
            }
        )
    }

    render(){
        if (this.state.error) {
            return <h1>{this.state.error}</h1>;
          }
        let average = (array) => array.reduce((a, b) => a+b) / array.length;
        return (this.state.distanceList.length && average(this.state.distanceList))
    }
}

export default DistanceRenderer